.page-header {
  border: 1px solid rgb(235, 237, 240);
  background: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  font-family: Avenir,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
  padding: 14px 39px 16px 39px;
  height: 64px;

  a {
    color: rgba(0, 0, 0, .45);
  }
}

.bread-crumb {
  margin-bottom: 12px;
}

.view-header {
  margin-bottom: 12px;
  .title {
    display: inline-block;
  }
  .code {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
    -webkit-touch-callout: none;
    user-select: none;
    float: right;
    margin: 8px 0 0 0;
    color: rgba(0, 0, 0, 0.45);
    a {
      color: rgba(0, 0, 0, .45);
    }
  }
}

.card {
  width: 1414px;
  margin: 20px auto;
}
