.magi-plugin-legend-table-right {
    position: absolute;
    top: 0;
    right: 0;

    .ant-table-row {
      cursor: pointer;
    }
  }


.chart-legend-table {
  .ant-table.ant-table-small {
    font-size: 12px;
  }
  .ant-table.ant-table-small .ant-table-thead > tr > th {
    padding: 3px 8px;
    font-weight: normal;
    color: rgba(44, 53, 66, 0.65);
  }
  .ant-table.ant-table-small .ant-table-thead .ant-table-column-sorters {
    padding: 0px;
  }
  .ant-table.ant-table-small .ant-table-tbody > tr > td {
    padding: 5px 8px;
    border-bottom: 0;
  }
  .ant-table-column-sorter-up,
  .ant-table-column-sorter-down {
    font-size: 10px;
  }
  .ant-table-wrapper.chart-legend-table {
    max-width: unset;
  }

  .ant-table table {
    width: auto;
  }
}
.chart-y-slider-container {
  position: absolute;
  left: 0;
  .ant-slider {
    display: none;
  }
  &:hover {
    background-color: rgba(24, 144, 255, 0.05);
    .corner-tip {
      display: block;
    }
  }
  &.active {
    background-color: #fff;
    .corner-tip {
      display: none;
    }
    .ant-slider {
      display: block;
    }
  }
  .corner-tip {
    position: absolute;
    width: 0;
    height: 0;
    display: none;
    overflow: hidden;
  }
  .corner-tip.tl {
    border: 6px solid transparent;
    top: 0;
    left: 0;
    border-top: 6px solid rgb(24, 144, 255);
    border-left: 6px solid rgb(24, 144, 255);
  }
  .corner-tip.tr {
    border: 3px solid transparent;
    top: 0;
    right: 0;
    border-top: 3px solid rgb(24, 144, 255);
    border-right: 3px solid rgb(24, 144, 255);
  }
  .corner-tip.bl {
    border: 3px solid transparent;
    bottom: 0;
    left: 0;
    border-bottom: 3px solid rgb(24, 144, 255);
    border-left: 3px solid rgb(24, 144, 255);
  }
  .corner-tip.br {
    border: 3px solid transparent;
    bottom: 0;
    right: 0;
    border-bottom: 3px solid rgb(24, 144, 255);
    border-right: 3px solid rgb(24, 144, 255);
  }
  .ant-slider-mark {
    display: none;
  }
  .ant-slider-container {
    float: right;
    height: 100%;
    margin-right: 5px;
  }
  .ant-slider-vertical {
    width: 18px;
    margin: 0;
    background-color: rgba(91, 143, 249, 0.15);
    padding: 0;
  }
  .ant-slider-track,
  .ant-slider-rail,
  .ant-slider-step {
    display: none;
  }
  .ant-slider-handle {
    height: 13px;
    width: 24px;
    background: url("https://gw.alipayobjects.com/zos/bmw-prod/536356f1-5cc7-4e3b-a929-ccb5dab284f0.svg");
    border-radius: unset;
    border: none;
    margin-left: -3px;
    &:focus {
      box-shadow: unset;
    }
  }
}
